<template>
    <div class="content">
        <every-day />
        <article-list :now-page="nowPage" @getTotal="getTotal"/>
        <turn-page :total-page="totalPage" :cur-page="nowPage" @turn-page="turnPage"/>
    </div>
</template>


<script>
import everyDay from "@/components/index/everyDay.vue"
import articleList from "@/components/index/articleList.vue"
import turnPage from "@/components/index/turnPage.vue"
export default {
    components:{
        everyDay,
        articleList,
        turnPage
    },
    data(){
        return{
            totalPage:10,
            nowPage:1,
            pageSize:2
        }  
    },
    mounted(){
        // this.$axios.get('/test',{
        //     params:{}
        // }).then(res => console.log(res))
      
    },
    methods:{
        turnPage(i){
            this.nowPage = i;
        },
        getTotal(val){
            this.totalPage = Math.ceil(val/this.pageSize);
        }
    }

}
</script>


