<template>
    <div class="article-list">
        <div class="article" v-for="item in articleList" :key="item.id">
            <div class="blog">
                <router-link :to="item.link" class="blog-title">{{item.title}}</router-link>
                <p>{{item.content}}</p>
            </div>
            
            <div class="tags">
                发布于
                <span>{{item.ctime}}</span>
                 | 浏览(<span>{{item.views}}</span>) | Tags : 
                <a href="#">{{item.tags}}</a>
            </div>
        </div>
    </div>
</template>


<script>
import { getDate } from '@/util/getDate.js'
export default {
    props:['nowPage'],
    mounted(){
       this.renderPage(); 
    },
    data(){
        return{
            pageSize:2,
            curPage:this.nowPage,
            articleList:[],
            tagObj:{
                tagFlag:'',
                tag:''
            }
        }
    },
    methods:{
        getBlog(){
             this.$axios.get('/queryBlogByPage',{
                params:{
                    page:this.pageSize,
                    curPage:this.curPage
                }
            }).then(res =>{
                const result = res.data.data;
                const list = [];
                for(let i = 0; i < result.length; i++){
                    const temp = {};
                    temp.title = result[i].title;
                    temp.content = result[i].content;
                    temp.ctime = getDate(result[i].ctime);
                    temp.views = result[i].views;
                    temp.tags = result[i].tags;
                    temp.id = result[i].id;
                    temp.link = '/detail?bid='+ result[i].id;
                    list.push(temp);
                }
                this.articleList = list;
            }).catch(err => console.log(err))
        },
        getBlogCount(){
             this.$axios.get('/queryTotalBlog',{
                params:{}
            }).then(res => {
                this.$emit('getTotal',res.data.data[0].count);
            }).catch(err => console.log(err))
        },
        getBlogByTag(tag){
            this.$axios.get('/queryBlogByTag',{
                params:{
                    page:this.pageSize,
                    curPage:this.curPage,
                    tag
                }
            }).then(res =>{
                // console.log(res)
                const result = res.data.data;
                const list = [];
                for(let i = 0; i < result.length; i++){
                    const temp = {};
                    temp.title = result[i].title;
                    temp.content = result[i].content;
                    temp.ctime = getDate(result[i].ctime);
                    temp.views = result[i].views;
                    temp.tags = result[i].tags;
                    temp.id = result[i].id;
                    temp.link = '/detail?bid='+ result[i].id;
                    list.push(temp);
                }
                this.articleList = list;
                // this.$emit('getTotal',res.data.data.len);
            }).catch(err => console.log(err))
        },
        getBlogByTagCount(tag){
            this.$axios.get('/queryBlogByTagCount',{
                params:{
                    tag
                }
            }).then(res => {
                this.$emit('getTotal',res.data.data[0].count);
            }).catch(err => console.log(err))
        },
        renderPage(){
            let tag = '';
            let tagFlag = '';
            let searchUrlParams = location.search.indexOf('?') > -1 ? location.search.split('?')[1]:"";
            if(searchUrlParams == ''){
                tag = searchUrlParams;
                tagFlag = '';
            }else if(searchUrlParams.split('=')[0] == 'tag'){
                    tag = decodeURIComponent(searchUrlParams.split('=')[1]);
                    tagFlag = 'tag';
            }else if(searchUrlParams.split('=')[0] == 's'){
                tag = decodeURIComponent(searchUrlParams.split('=')[1]);
                tagFlag = 's';  
            }
            this.tagObj = {
                tagFlag,
                tag
            }
            this.getRenderData();
        },
        getRenderData(){
            if(this.tagObj.tagFlag == ''){
                this.getBlog();
                this.getBlogCount();
            }else if(this.tagObj.tagFlag == 'tag'){
                this.getBlogByTag(this.tagObj.tag);
                this.getBlogByTagCount(this.tagObj.tag);
            }else if(this.tagObj.tagFlag == 's'){
                this.getBlogBySearchWord(this.tagObj.tag);
                this.getBlogBySearchWorCount(this.tagObj.tag);
            }
        },
        getBlogBySearchWord(tag){
            this.$axios.get('/queryBlogBySearchWord',{
                params:{
                    searchWord:tag,
                    page:this.pageSize,
                    curPage:this.curPage
                }
            }).then(res =>{
                const result = res.data.data;
                const list = [];
                for(let i = 0; i < result.length; i++){
                    const temp = {};
                    temp.title = result[i].title;
                    temp.content = result[i].content;
                    temp.ctime = getDate(result[i].ctime);
                    temp.views = result[i].views;
                    temp.tags = result[i].tags;
                    temp.id = result[i].id;
                    temp.link = '/detail?bid='+ result[i].id;
                    list.push(temp);
                }
                this.articleList = list;
            }).catch(err => console.log(err))
        },
        getBlogBySearchWorCount(tag){
            this.$axios.get('/queryBlogBySearchWorCount',{
                params:{
                     searchWord:tag
                }
            }).then(res =>{
               this.$emit('getTotal',res.data.data[0].count);
            }).catch(err => console.log(err))
        }
    },
    watch:{
        nowPage(val){
            this.curPage = val;
            this.getRenderData();
        }
    }
}
</script>


<style lang="scss">
    @import "@/assets/css/index/article-list.scss"
</style>