<template>
    <div class="every-day">
        <random-title :title="title">
            <span>♬</span>
        </random-title>
        <p class="sentence">{{everyDay.content}}</p>
        <div class="translate">
            <span>{{everyDay.translate}}</span>
            <span>{{everyDay.author}}</span>
        </div>
    </div>
</template>


<script>
import randomTitle from '../default/randomTitle.vue'
export default {
    components:{
        randomTitle
    },
    data(){
        return {
            title:'每日一句',
            everyDay:{
                 content:'The world spins. We stumble on. It is enough.',
                 translate:'世界快速旋转，我们踉跄前行，就已足够。',
                 author:'———— Colum McCann'
            }
           
        }
    },
    mounted(){
        
        this.$axios.get('/queryEveryDay',{
            params:{}
        }).then(res => {
            this.everyDay.content = res.data.data[0].content;
        }).catch(err => console.log(err))
    }
}
</script>


<style lang="scss">
    @import "@/assets/css/index/every-day.scss"
</style>